function input(props){

    return (
        <div>
            <textarea
                id='editor'
                value={props.input}
                onChange={props.change}
            >   What
            -- 
            manner
            ==
            of *madness* **is** this
            what
            ___
            

            </textarea>
            <style jsx>{`
                #editor {
                    width: 800px;
                    height: 200px;
                    margin: auto;
                } 
            `}</style>
        </div>
    )
}

export default input;